<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <div v-for="(o) in content" :key="o">
        <div v-for="(a) in o" :key="a">
          <router-link :to="'/' + type +'?id=' + a.id + '&type=' + type">
            <div class="list-introduce">
              <dev class="title">
                <b>{{a.title}}</b>
              </dev>
              <br/>
              <span class="img">
                <el-image :key="a.img" :src="a.img" :fit="none"></el-image>
              </span>
              <p>
                {{a.remark}}
              </p>
              <span class="">
                <b>作者：</b>{{a.teacher}} <b>时间：</b>{{a.time}}
              </span>
            </div>
          </router-link>
          <el-divider></el-divider>
        </div>
      </div>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import { article } from '../../../static/js/article.js'
import { tool } from '../../../static/js/tool.js'
import { project } from '../../../static/js/project.js'
import { vedio } from '../../../static/js/vedio.js'
import { doc } from '../../../static/js/doc.js'

export default {
  data () {
    return {
      type: this.$route.query.type,
      content: []
    }
  },
  created () {
    this.initData()
  },
  methods: {
    initData: function () {
      switch (this.type) {
        case 'article':
          this.content = article
          break
        case 'tool':
          this.content = tool
          break
        case 'project':
          this.content = project
          break
        case 'vedio':
          this.content = vedio
          break
        case 'doc':
          this.content = doc
          break
      }
    }
  }
}
</script>

<style scoped>
.list-introduce {
  padding: 10px;
  line-height: 23px;
  height: 190px;
}
.img {
  margin: 8px;
  float: left;
  width: 200px;
}
</style>



