<template>
  <div>
    <div class="introduce">
      <b>介绍：</b>{{ content.remark }}
    </div>
    <div v-for="(o) in content.json" :key="o">
      <div class="img" v-if="o.type === 'img'">
        <img :src="o.content"/>
      </div>
      <div class="img_small" v-if="o.type === 'img_small'">
        <el-row :gutter="24">
          <div v-for="(i) in o.content" :key="i">
            <el-col :span="8">
              <img :src="i"/>
            </el-col>
          </div>
        </el-row>
      </div>
      <div class="img_big" v-if="o.type === 'img_big'">
        <el-row :gutter="24">
          <div v-for="(i) in o.content" :key="i">
            <el-col :span="24">
              <img :src="i"/>
            </el-col>
          </div>
        </el-row>
      </div>
      <div class="text" v-if="o.type === 'text'">
        {{o.content}}
      </div>
      <div class="tool" v-if="o.type === 'tool'">
        官网下载地址：<a :href="o.content" target="_Blank"><span>{{o.content}}</span></a>
      </div>
      <div class="doc" v-if="o.type === 'doc'">
        百度网盘地址：<a :href="o.url" target="_Blank"><span>{{o.url}}</span></a>
        <br/>
        密码：<span>{{o.password}}</span>
        <br/>
        <img :src="doc.img"/>
      </div>
      <div class="project" v-if="o.type === 'project'">
        百度网盘地址：<a :href="o.url" target="_Blank"><span>{{o.url}}</span></a>
        <br/>
        密码：<span>{{o.password}}</span>
        <br/>
        <img :src="project.img"/>
      </div>
      <div class="vedio" v-if="o.type === 'vedio'">
        百度网盘地址：<a :href="o.url" target="_Blank"><span>{{o.url}}</span></a>
        <br/>
        密码：<span>{{o.password}}</span>
        <br/>
        <img :src="vedio.img"/>
      </div>
    </div>
    <div>
      <p v-if="pro != null">上一篇：
        <a :href="'/#/' + type +'?id=' + pro.id + '&type=' + type" target="_Blank"><span>{{pro.title}}</span></a>
      </p>
      <p v-if="next != null">下一篇：
        <a :href="'/#/' + type +'?id=' + next.id + '&type=' + type" target="_Blank"><span>{{next.title}}</span></a>
      </p>
    </div>
  </div>
</template>

<script>
import { article } from '../../../static/js/article.js'
import { tool } from '../../../static/js/tool.js'
import { project } from '../../../static/js/project.js'
import { vedio } from '../../../static/js/vedio.js'
import { doc } from '../../../static/js/doc.js'

export default {
  data () {
    return {
      id: this.$route.query.id,
      type: this.$route.query.type,
      content: [],
      pro: [],
      next: []
    }
  },
  created () {
    this.initData()
  },
  methods: {
    initData: function () {
      switch (this.type) {
        case 'article':
          this.content = article.data[this.$route.query.id - 1]
          this.pro = article.data[this.$route.query.id - 2]
          this.next = article.data[this.$route.query.id]
          break
        case 'tool':
          this.content = tool.data[this.$route.query.id - 1]
          this.pro = tool.data[this.$route.query.id - 2]
          this.next = tool.data[this.$route.query.id]
          break
        case 'project':
          this.content = project.data[this.$route.query.id - 1]
          this.pro = project.data[this.$route.query.id - 2]
          this.next = project.data[this.$route.query.id]
          break
        case 'vedio':
          this.content = vedio.data[this.$route.query.id - 1]
          this.pro = vedio.data[this.$route.query.id - 2]
          this.next = vedio.data[this.$route.query.id]
          break
        case 'doc':
          this.content = doc.data[this.$route.query.id - 1]
          this.pro = doc.data[this.$route.query.id - 2]
          this.next = doc.data[this.$route.query.id]
          break
      }
    }
  }
}
</script>

<style>
.introduce {
  padding: 10px;
  background-color: #EFF2F7;
  margin-bottom: 10px;
  border: solid 1px #F3F3F3;
  line-height: 23px;
}
</style>



