<template>
  <div class="title">
    <b>{{ content.title }}</b>
    <div>
      <b>作者：</b>{{ content.teacher }}
      <b>时间：</b>{{ content.time }}
      <span v-if="this.type === 'article'">
        <b>售价：</b>{{ content.price }}
      </span>
    </div>
  </div>
</template>

<script>
import { article } from '../../../static/js/article.js'
import { tool } from '../../../static/js/tool.js'
import { project } from '../../../static/js/project.js'
import { vedio } from '../../../static/js/vedio.js'
import { doc } from '../../../static/js/doc.js'

export default {
  data () {
    return {
      id: this.$route.query.id,
      type: this.$route.query.type,
      content: []
    }
  },
  created () {
    this.initData()
  },
  methods: {
    initData: function () {
      switch (this.type) {
        case 'article':
          this.content = article.data[this.id - 1]
          break
        case 'tool':
          this.content = tool.data[this.id - 1]
          break
        case 'project':
          this.content = project.data[this.id - 1]
          break
        case 'vedio':
          this.content = vedio.data[this.id - 1]
          break
        case 'doc':
          this.content = doc.data[this.id - 1]
          break
      }
    }
  }
}
</script>

<style scoped>
.text {
  text-indent:25px;
}
.title div {
  font-size: 15px;
  color: red;
  margin-top: 10px;
}
</style>



